.main-title {
  font-style: normal;
  font-weight: 600;
  font-size: clamp(30px, 5vw, 60px);
  line-height: 120%;
  font-family: var(--font-family);
  text-align: center;
  color: var(--color-a);
  text-transform: uppercase;
}


.show-more {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 202px;
  height: 48px;

  font-style: normal;
  font-weight: 700;
  font-size: 15px;
  font-family: var(--font-family);
  color: var(--color-a);
  text-transform: uppercase;

  border: 2px solid var(--plus-bg);

  transition: all 0.4s;

  &:hover {
    background-color: var(--plus-bg);
    transform: scale(1.05);
  }

  &.disable {
    opacity: 0.3;

    pointer-events: none;
  }
}

.error-message {
  font-style: normal;
  font-weight: 400;
  font-size: clamp(13px, 1.25vw, 15px);
  line-height: 110%;
  font-family: var(--font-family);
  color: var(--plus-bg);
}

.red-border {
  padding: clamp(10px, 1.25vw, 15px) clamp(15px, 1.667vw, 20px);

  font-style: normal;
  font-weight: 700;
  font-size: 15px;
  line-height: 100%;
  font-family: var(--font-family);
  color: var(--color-d);
  text-transform: uppercase;

  border: 2px solid var(--plus-bg);
  transition: all .4s;

  &:hover {
    background-color: var(--plus-bg);
    color: var(--color-a);
  }
}

.forgot-pass {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 100%;
  text-decoration-line: underline;
  color: var(--color-i);
}
